* {
  box-sizing: border-box;
}

ul {
  padding: 0;
}

.fz-20 {
  font-size: 20px;
}

html {
  font: 900 11px/20px 'pfbeausanspro';
}

body {
  margin: 0;
  color: $color-text;
}

.container {
  max-width: 1035px;
  padding: 0 15px;
  margin: 0 auto;
}

.garant {
  position: absolute;
  top: 51px;
  left: -77px;
  transform: rotate(-47deg);
  font-weight: 400;
  text-align: center;
  width: 300px;
  overflow: hidden;
  background: linear-gradient(180deg, #e9bb00 15%,
    #f5dc5e 20%, #ecbf00 37%, #f7d854 46%,
    #ffe100 60%, #f9eda4 65%, #f2d224 100%);
  span {
    display: block;
    font-size: 24px;
    font-weight: 400;
  }
}

.logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 100px;
}
