.menu {
  background-color: $color-dark-section;
  padding: 20px 0;
  &__items {
    display: flex;
    justify-content: center;
    list-style: none;
  }
  &__item {
    display: flex;
    align-items: flex-end;
    border-radius: 10px;
    transition: .3s all;
    margin: 0 10px;
    &:hover {
      background-color: $color-main;
    }
  }
  &__link {
    text-align: center;
    color: #fff;
    text-decoration: none;
    padding: 20px 25px;
  }
  &__img {
    max-height: 65px;
    max-width: 65px;
    object-fit: contain;
  }
}
