.header {
  padding: 100px 0 60px;
  background-color: #efeff0;
  &__items {
    list-style-type: none;
    display: flex;
    justify-content: center;
  }
  &__item + .header__item {
    margin-left: auto;
  }
  &__item {
    text-transform: uppercase;
    cursor: pointer;
    position: relative;
    text-align: center;
    border-bottom: 1px solid transparent;
    transition: .5s all;
    &:hover {
      border-bottom: 1px solid #000;
    }
    #city {
      cursor: pointer;
      border: none;
      color: $color-second;   
      text-decoration: underline;
      text-decoration-style: dotted;
      font-weight: 900;
      font-family: 'pfbeausanspro';
      background-color: #efeff0;
      text-transform: uppercase;
      font-size: 11px;
    }
    option {
      text-decoration: underline;
      color: $color-text;
    }
    &--basket {
      font-weight: 400;
      ::before {
        content: "";
        display: block;
        width: 124px;
        height: 76px;
        position: absolute;
        top: -78px;
        left: -30px;
        background-image: url('../img/order.png');
        background-repeat: no-repeat;
      }
    }
    &--it {
      cursor: initial;
      font-size: 20px;
      justify-items: center;
      transform: translateX(-10px);
    }
    &--call {
      position: relative;
      padding: 0 0 0 25px;
      ::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        background-image: url('../img/icons/phone.png');
      }
    }
  }
  &__basket {
    color: $color-second;   
    text-decoration: underline;
    text-decoration-style: dotted;
    font-weight: 900;
  }
  &__call {
    background-color: #efeff0;
    border: none;
    border-bottom: 1px dotted #000;
    cursor: pointer;
    font-weight: 900;
    text-transform: uppercase;
    font-size: 11px;
    padding: 0;
  }
  &__number {
    text-decoration: none;
    font-size: 16px;
    color: $color-text;
    span {
      background-color: $color-main;
    }
  }
  &__wrapper-search {
    position: relative; 
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 1px;
      background: linear-gradient(90deg, $color-light-text 35%, transparent 35%, transparent 65%, $color-light-text 65%, $color-light-text 100%);
      opacity: .5;
      order: -1;
    }
    &::after {
      content: '';
      position: absolute;
      bottom: -30px;
      left: 50%;
      transform: translateX(-50%);
      width: 80%;
      height: 1px;
      opacity: .5;
      order: -1;
      background-color: $color-light-text;
    }
  }
  &__search {
    background-color: $color-dark-section;
    font-size: 10px;
    font-weight: 400;
    padding: 10px 12px;
    border-radius: 5px;
    width: 190px;
    display: block;
    margin: 15px auto 0;
    color: #fff;
    position: relative;
    border: none;
    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 20px;
      height: 20px;
      background: url('../img/icons/search.png');
    }
  }
  &__text {
    font-weight: 400;
    text-align: center;
    margin: 60px auto 0;
    font-size: 15px;
    position: relative;
    div {
      font-size: 23px;
      font-weight: 900;
      font-family: 'futuraround';
      text-transform: uppercase;
      margin-bottom: 7px;
    }
    a {
      border-bottom: 1px dotted $color-second;
      color: $color-second;
      text-decoration: none;
    }
    &::after {
      content: '';
      position: absolute;
      bottom: -30px;
      left: 50%;
      transform: translateX(-50%);
      width: 80%;
      height: 1px;
      opacity: .5;
      order: -1;
      background-color: $color-light-text;
    }
  }
}