.person {
  background-color: $color-light-section;
  &__wrapper {
    display: flex;
    text-transform: uppercase;
    overflow: hidden;
  }
  &__block {
    position: relative;
    width: 50%;
    padding: 0 0 40px;
    overflow: hidden;
    img {
      position: absolute;
      top: 197px;
      right: -95px;
      object-fit: contain;
      display: block;
      max-width: 430px;
      max-height: 100%;
    }
  }
  &__title {
    font-size: 59px;
    line-height: 50px;
    div {
      font-size: 35px;
      font-weight: 400;
      margin-top: 15px;
      line-height: 40px;
    }
  }
  &__items {
    list-style-type: none;
  }
  &__item {
    padding: 10px 20px;
    transition: .3s all;
    border-radius: 10px;
    width: 70%;
    cursor: pointer;
    &:hover {
      background-color: $color-main;
    }
  }
  &__legal-item {
    &:hover {
      background-color: #303030;
      color: #fff;
    }
  }
}
